import React from "react";
import { Link } from "gatsby";

// Styles
import "./DownloadAsset.styles.scss";

// Components
import DownloadAssetImage from "./DownloadAssetImage.component";
import DividerArrow from "../DividerArrow/DividerArrow.component";
import SpanishFlag from "./SpanishFlag.component";

const DownloadAsset = ({ mainTitle, body, englishLink, spanishLink }) => (
    <div className="download-asset-container">
        <DividerArrow />
        <div className="download-asset">
            <DownloadAssetImage />
            <div className="download-asset-text">
                <h2>{ mainTitle }</h2>
                <div dangerouslySetInnerHTML={{ __html: body }} />
                <div className="download-asset-links">
                    <Link to={ englishLink }>Download PDF</Link>
                    <Link to={ spanishLink }><SpanishFlag />Also available in Spanish</Link>
                </div>
            </div>
        </div>
    </div>
)

export default DownloadAsset;