import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"

import Layout from "../components/layout"
import SEO from "../components/seo"

// Styles
import "./weaning.styles.scss";

// Components
import DownloadAsset from "../components/DownloadAsset/DownloadAsset.component";
import WeaningBenefits from "../components/WeaningBenefits/WeaningBenefits.component";
import HomeProducts from "../components/HomeProducts/HomeProducts.component";

const Weaning = () => {

  const data = useStaticQuery(graphql`
    query {
      weaning: allMarkdownRemark(filter: {frontmatter: {title: {eq: "Weaning"}}}) {
        edges {
          node {
            frontmatter {
              title
              subtitle
              headerImage {
                childImageSharp {
                  fluid {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
              productImage {
                childImageSharp {
                  fluid {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
            html
          }
        }
      }
      weaningDownload: allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(weaning/downloads)/"}}) {
        edges {
          node {
            frontmatter {
              mainTitle
              spanishLink
              englishLink
              image
            }
            html
          }
        }
      }
    }
  `)
  
  return (
    <Layout>
      {
        data.weaning.edges.map(({node}) => (
          <div>
            <SEO title="Weaning" />
            <Img fluid={ node.frontmatter.headerImage.childImageSharp.fluid } />
            <div className="weaning">
              <h2>{ node.frontmatter.title }</h2>
              <p>{ node.frontmatter.subtitle }</p>
            </div>
            <WeaningBenefits />
            <div className="home-products">
                <div className="content" dangerouslySetInnerHTML={{ __html: node.html }} />
                <Img fluid={ node.frontmatter.productImage.childImageSharp.fluid } />
            </div>
          </div>
        ))
      }
      {
        data.weaningDownload.edges.map(({node}) => (
          <DownloadAsset 
            mainTitle={node.frontmatter.mainTitle} 
            body={node.html} 
            englishLink={node.frontmatter.englishLink} 
            spanishLink={node.frontmatter.spanishLink} 
          />
        ))
      }
    </Layout>
  )
}

export default Weaning
